'use client';

import { useEffect, useRef, useState } from 'react';
import type DPlayer from 'dplayer';
import 'dplayer/dist/DPlayer.min.css';

interface VideoPlayerProps {
  initialUrl: string;
  title?: string;
  cover?: string;
  id: string;
}

export default function VideoPlayer({ initialUrl, title, id }: VideoPlayerProps) {
  const containerRef = useRef<HTMLDivElement>(null);
  const playerRef = useRef<DPlayer | null>(null);
  const [currentUrl, setCurrentUrl] = useState(initialUrl);

  useEffect(() => {
    console.log('Current Video URL:', currentUrl);
    console.log('Video Title:', title);

    // 初始化播放器
    const initializePlayer = () => {
      if (containerRef.current) {
        import('dplayer').then((DPlayerModule) => {
          if (playerRef.current) {
            playerRef.current.destroy(); // 销毁当前播放器实例
          }
          const DPlayer = DPlayerModule.default;
          playerRef.current = new DPlayer({
            container: containerRef.current as HTMLElement,
            video: {
              url: currentUrl,
              type: 'auto',
            },
            title: title,
            theme: '#FADFA3',
            autoplay: false,
            preload: 'auto',
            volume: 0.7,
            playbackSpeed: [0.5, 0.75, 1, 1.25, 1.5, 2],
            hotkey: true,
            logo: '/logo.png',
            subtitle: {
              url: '',
              type: 'webvtt',
              fontSize: '25px',
              bottom: '10%',
              color: '#fff',
            },
          });
        });
      }
    };

    initializePlayer();

    return () => {
      if (playerRef.current) {
        playerRef.current.destroy();
        playerRef.current = null;
      }
    };
  }, [currentUrl, title, id]);

  const updateVideoUrl = (newUrl: string) => {
    console.log('切换到新视频 URL:', newUrl);
    setCurrentUrl(newUrl);
  };

  return (
    <div className="w-screen h-screen">
      <div ref={containerRef} className="w-full h-full"></div>
      <button onClick={() => updateVideoUrl('/new/video/url.mp4')}>切换视频</button>
    </div>
  );
} 